@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
.header{width:100%;display:flex;flex-direction:row;justify-content:flex-end}.header .change-locale-btn{font-size:20px;font-weight:bold;background-color:transparent;text-transform:uppercase;padding:10px;cursor:pointer}

.notification{position:absolute;bottom:20px;right:20px;z-index:99;min-width:20%;border:1px solid #ffeeba !important;border-radius:5px;color:#856404;background-color:#fff3cd;padding:20px;font-size:20px;font-weight:bold;transform-origin:right;animation:notify 3s ease-in-out infinite}@keyframes notify{0%{transform:scaleX(0);opacity:0}10%{transform:scaleX(1);opacity:1}16%{transform:scale(1)}55%{transform:scaleX(1);opacity:1}65%{transform:scaleX(0.25);opacity:0}95%{transform:scaleX(0.25)}100%{transform:scaleX(0)}}

.nav{font-size:16px;padding:36px 32px;background:rgba(245,245,244,0.7);-webkit-backdrop-filter:blur(20px);backdrop-filter:blur(20px);border-radius:16px}.nav__logo{width:100%;padding-bottom:36px}.nav__link{display:flex;align-items:center;width:100%;padding:12px 27px;color:#525252;text-decoration:none}.nav__link--active{background:#333333;box-shadow:0px 0px 1px rgba(40,41,61,0.04),0px 2px 4px rgba(96,97,112,0.16);border-radius:8px;color:#f5f5f4}.nav__link svg{margin-right:22px}

.container{display:flex;width:100vw;height:100vh;background:#ebebea;padding:32px}.container__right{width:100%}.main{padding:52px}



*{margin:0;padding:0;border:none;outline:none;box-sizing:border-box}*{font-family:"Inter", sans-serif !important}body{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

